import topSearch from "@/components/topSearch.vue";
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import msDropdownMenu from '@/components/ms-dropdown/dropdown-menu.vue'
import msDropdownItem from '@/components/ms-dropdown/dropdown-item.vue'
let _this,timer,onOff
export default {
	components:{
		topSearch,
		uniLoadMore,
		msDropdownMenu,
		msDropdownItem
	},
	data() {
		return {
			customerList:[],
			customLevel: 0,
			levelList:[],
			keyword:'',
			postCode: '',
			pageNum: 1,
			pageSize: 20
		};
	},
	onLoad:function(){
		_this = this
		this.getCompanyLevel()
		this.postCode = uni.getStorageSync('pupDefault')
		onOff = 1
		this.fetchList()
	},
	onReachBottom: function () {
	    
	       if (timer != null) {
	                  clearTimeout(timer);
	              }
	         timer = setTimeout(function() {
	              _this.fetchMoreList();
	          }, 1000);

	},
	methods:{
		async fetchList(){
			let data
			if (onOff==1){
				data ={
					keyword: this.keyword,
					postCode: this.postCode,
					level: -1,
					pageNum: this.pageNum,
					pageSize: this.pageSize
				}
				onOff = 0
			}else {
				data ={
					keyword: this.keyword,
					postCode: this.postCode,
					level: this.customLevel,
					pageNum: this.pageNum,
					pageSize: this.pageSize
				}
			}
			
			const res = await this.$http.get('/cm/level_list',{data: data})
			this.customerList = res.data.data.list
			console.log(res)
		},
		async fetchMoreList(){
			
			this.pageSize  +=  20
			
			let	data ={
					keyword: this.keyword,
					postCode: this.postCode,
					level: this.customLevel,
					pageNum: this.pageNum,
					pageSize: this.pageSize
				}
			
			
			const res = await this.$http.get('/cm/level_list',{data: data})
			this.customerList = res.data.data.list
			console.log(res)
		},
		
		filterInput:function(){
			this.fetchList()
		},
		async getCompanyLevel(){
			const res = await this.$http.get('/choose/company_level',{})
			let tmp = res.data.data.list
			tmp.forEach((item)=>{
				this.levelList.push({text:item.label,value:item.id})
			})
			this.levelList.unshift({text:'全部等级',value: -1})
			
		},
		tapSearch: function (value) {
		  uni.showLoading({
		    title: '搜索中...'
		  });
		  this.keyword = value
		  this.fetchList()
		  setTimeout(function() {
		  		  uni.hideLoading();
		  }, 2000);
		  
		},
		toMicroShop:function(e){
			let data = JSON.stringify(e)
			uni.navigateTo({
				url: `./microShop?data=${data}`
				
			});
		}
	}
	
}
